html, body {
  width: 100%;
  height: 100%;
}

body {
  font: 100% 'Microsoft yahei', , 'HanHei SC', 'PingFang SC', 'Helvetica Neue', 'Helvetica', 'STHeitiSC-Light', Tahoma, Verdana, Arial, sans-serif;
}

* {
  box-sizing: border-box;
}

a {
  text-decoration: none;
}

i.fa {
  color: $brand-primary;
  width: 1em;
}

#pjax-container {
  display: inline-block;
  position: absolute;
  z-index: $zindex-body;
  width: 100%;
  height: 100%;
  padding-bottom: $footer-height;
}


@media only screen and ( max-width: $mobile-width ) {
  //#pjax-container,
  //.main article{
  //  position: relative;
  //  height: auto;
  //}
}

.progressjs-inner {
  height: 3px !important;
  background-color: $brand-primary !important;
}

@media only screen and (max-width: $mobile-width) {
  .only-desktop {
    display: none;
  }
}

@media only screen and (min-width: $mobile-width + 1) {
  .only-mobile {
    display: none;
  }
}

@font-face {
  font-family: 'source-han-sans-cn';
  src: url('../fonts/KaiGenGothicSC-Normal.eot');
  src:
        url('../fonts/KaiGenGothicSC-Normal.eot?#font-spider') format('embedded-opentype'),
        url('../fonts/KaiGenGothicSC-Normal.woff') format('woff'),
        url('../fonts/KaiGenGothicSC-Normal.ttf') format('truetype'),
        url('../fonts/KaiGenGothicSC-Normal.svg') format('svg');
  font-weight: normal;
  font-style: normal;
}